export default {
    namespaced: true,
    state: {
        partnerInfo: [] // Vue
    },
    mutations: { 
        clear(state, val) {
            state.partnerInfo = [];
        },    
        updateItem(state, obj) {    
            // ,index
            let index = obj.index;
            delete obj.index;
            state.partnerInfo.splice(index, 1, obj);
        },
        addItem(state, item) {
            state.partnerInfo.push(item);
        },
        updateItems(state, items) {
            state.partnerInfo = items;
        },
        removeItem(state, index) {
            state.partnerInfo.splice(index, 1);
        }
    }
};


