import Vue from 'vue';

/**
 * privilegeOption
 * {
 *  "addBtn": "/storeManagement#addBtn"
 * }
 *
 * privileges.addBtn
 */
export default {
    data() {
        return {
            privileges: {

            }
        };
    },
    // 
    created() {
        if(this.privilegeOption) {// 
            let urls = [];
            let mapping = {

            };// urlkey
            let privileges = {

            };
            for(let key in this.privilegeOption) {
                urls.push(this.privilegeOption[key]);
                privileges[key] = false;// falsetrue,fasle
                mapping[this.privilegeOption[key]] = key;
            }
            Vue.http.post(Vue.apiUrl.common.privileges, {
                urls
            }).then((data)=>{
                if(data.data.data) {
                    let key;
                    for(let d of data.data.data) {
                        key = mapping[d.url];
                        privileges[key] = !!d.hasAuth;
                    }

                    this.privileges = privileges;
                }
            });
            // ,;
            // this.privileges = privileges;
        }
    }
};



