-->
<template>
    <div>
        <el-select
            :disabled="disabled"
            v-model="areaDress"
            filterable
            remote
            clearable
            reserve-keyword
            :placeholder="keyWords"
            :remote-method="remoteMethod"
            :loading="loading" @change="handleChange" style="width:100%;" value-key="name" @focus="clearOption">
            <!--value-key-->
            <el-option
                v-for="(item,index) in options"
                :key="item.name"
                :label="item.name"
                :value="item">
            </el-option>
        </el-select>
    </div>
</template>

<script>
export default {
    name:'territory',
    props:{
        "startLevel":{
            type:Number,
            default:2// 12345
        },
        "endLevel":{
            type:Number,
            default:4//
        },
        "placeholder":String,
        "value":{},//v-module;
        "disabled": {
          type: Boolean,
          default: false
        }
    },
    data(){
        return {
            loading:false,
            areaDress:this.value,//;
            options:[],//;
            keyWords:this.placeholder||''
        }
    },
    created(){
        if(this.value&&JSON.stringify(this.value) !== "{}"){
            this.options=[{
                id:this.areaDress.id,
                name:this.areaDress.name
            }];
        }
    },
    methods:{
        clearOption(){
            this.options=[];
        },
        clear(){
            this.areaDress={};
        },
        remoteMethod(query){
            // query;
            if(query==''){
                this.options=[];
            }
            if(query!==''){
                this.loading=true;
                let self=this;
                // ,query,startLevel,endLevel,,options,loading=false;
                this.$http.get(this.$apiUrl.area.search+'?name='+query+'&startLevel='+this.startLevel+'&endLevel='+this.endLevel)
                    .then(function(data){
                        let ret=data.data.data;
                        self.options=[];//;
                        // ();
                        if(self.endLevel==3){
                            for(var i in ret){
                                self.options.push({
                                    id:[ret[i].provinceId,ret[i].cityId],
                                    name:ret[i].provinceName+'-'+ret[i].cityName
                                });
                            }
                        };
                        // ;
                        if(self.endLevel==4){
                            for(var i in ret){
                                //,;
                                if(ret[i].regionName){
                                    self.options.push({
                                        id:[ret[i].provinceId,ret[i].cityId,ret[i].regionId],
                                        name:ret[i].provinceName+'-'+ret[i].cityName+'-'+ret[i].regionName
                                    });
                                }else{
                                    self.options.push({
                                        id:[ret[i].provinceId,ret[i].cityId],
                                        name:ret[i].provinceName+'-'+ret[i].cityName,
                                    });
                                }
                            }
                        };
                        // ;
                        if(self.endLevel==5){
                            for(var i in ret){
                                self.options.push({
                                    name:ret[i].provinceName+'-'+ret[i].cityName+'-'+ret[i].regionName+'-'+ret[i].townName,
                                    id:[ret[i].provinceId,ret[i].cityId,ret[i].regionId,ret[i].townId],
                                });
                            }
                        };
                        self.loading=false;
                    })
                    .catch(function(err){
                        console.log(err);
                    })
            }
        },
        handleChange(val){
            console.log(val,'');
            this.options=[];
            // v-model;
            this.$emit('input',val);
        }
    },
    watch:{
        // ;
        "value":{
            handler(val){
                this.areaDress=val;
                this.options=[{
                    id:this.areaDress.id,
                    name:this.areaDress.name
                }];
            },
            deep:true
        },
        
    }
}
</script>

<style>

</style>





