<template>
    <div>
        <div class="search-result" style="margin-bottom:20px;" >{{pagination.total}}</div>
        <el-table :data="waitAuditList" border style="width: 100%">
            <el-table-column prop="targetName" label="" align="center" ></el-table-column>
            <el-table-column prop="targetTypeName" label="" align="center" ></el-table-column>
            <el-table-column prop="subbmitterName" label="" align="center" ></el-table-column>
            <el-table-column prop="submitTime" label="" align="center"></el-table-column>
            <el-table-column prop="operateRemark" label="" align="center"></el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" @click="audit(scope.$index, scope.row)" type="text" v-show="privileges.endJoinList"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pagination.currentPage"
                :page-sizes="[10, 20, 50, 100,500]"
                :page-size="pagination.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagination.total">
            </el-pagination>
        </div>
    </div>  
</template>

<script>
import PrivilegeMixin from '@/utils/privilege';
export default {
    name:'waitAudit',
    mixins: [PrivilegeMixin],
    data(){
        return {
            privilegeOption: {
                "endJoinList": "/professionEndResult#endJoinList"
            },
            targetState:"1",//
            // 
            pagination:{
                currentPage:1,//1;
                pageSize:10,//10
                total:null//
            },
            waitAuditList:[]
        }
    },
    created(){
        this.requestList();
    },
    methods:{
        //
        handleSizeChange(val) {
            this.pagination.pageSize=val;
            this.requestList();
        },
        //
        handleCurrentChange(val) {
            this.pagination.currentPage=val;
            this.requestList();
        },
        audit(index,row){
            this.$router.push({name:"ProfessionEndVerify",query:{targetId:row.targetId,targetType:row.targetType}})
        },
        // ;
        requestList(){
            let self=this;
            let form={
                targetState:this.targetState,
                currentPage:this.pagination.currentPage,
                pageSize:this.pagination.pageSize
            }
            this.$http.post(this.$apiUrl.professionEnd.list,form,{
                loading: {
                    fullscreen: true
                }
            })
                .then(function(data){
                    self.pagination.total=data.data.data.total;
                    self.waitAuditList=data.data.data.data;
                })
                .catch(function(err){
                    console.log(err,'waitAudit');
                });
        }
    }
}
</script>

<style scoped>
    .pagination{
        margin-top: 100px;
        text-align: center;
    }
</style>





