<template>
    <div>
        <div class="search-result" > {{pagination.total}}</div>
        <el-table :data="waitAuditList" border style="width: 100%">
            <el-table-column prop="name" label="" align="center" ></el-table-column>
            <el-table-column prop="cityName" label="" align="center" ></el-table-column>
            <el-table-column prop="submitterName" label="" align="center" ></el-table-column>
            <el-table-column  label="" align="center" >
                <template slot-scope="scope">
                    <div><span class="circle"></span><span></span></div>
                </template>
            </el-table-column>
            <el-table-column prop="auditTime" label="" align="center"></el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" @click="audit(scope.$index, scope.row)" type="text" v-show="privileges.agentAgainAudit"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pagination.currentPage"
                :page-sizes="[10, 20, 100, 500]"
                :page-size="pagination.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagination.total">
            </el-pagination>
        </div>
    </div>  
</template>

<script>
import PrivilegeMixin from '@/utils/privilege';

export default {
    name:'waitAudit',
    mixins: [PrivilegeMixin],
    data(){
        return {
            privilegeOption: {//
                "agentAgainAudit": "/professionAuditResult#agentAgainAudit"
            },
            // 
            pagination:{
                currentPage:1,//1;
                pageSize:10,//10
                total:null//
            },
            waitAuditList:[],//
        }
    },
    created(){
        this.requestList();
    },
    methods:{
        //
        handleSizeChange(val) {
            this.pagination.pageSize=val;
            this.requestList();
        },
        //
        handleCurrentChange(val) {
            this.pagination.currentPage=val;
            this.requestList();
        },
        audit(index,row){
            // ;
            // this.$router.push({name:'ProfessionAuditVerify',query:{id:row.id}});
        },
         // ;
        requestList(){
            let self=this;
            let requestInfo={
                auditType:"1",//
                currentPage:this.pagination.currentPage,
                pageSize:this.pagination.pageSize,
                targetState:"1"//1,2.3
            }
            this.$http.post(this.$apiUrl.professionAudit.auditList,requestInfo)
                .then(function(data){
                    self.pagination.total=data.data.data.total;
                    self.waitAuditList=data.data.data.data;
                    console.log('');
                })
                .catch(function(err){
                    console.log(err);
                });
        }
    }
}
</script>

<style scoped>
    .pagination{
        margin-top: 100px;
        text-align: center;
    }
</style>





