// 
export let Validator = {
    name(url, value, callback) {
        // 
        if(value == '' || value == null) {
            callback();
            return;
        }

        let reg = /^[0-9a-zA-Z\u4e00-\u9fa5]*$/;
    
        if(reg.test(value)) {
            callback();
        } else {
            callback(new Error('!'));
        }
    },

    mobile(rule, value, callback) {
        if(value == '' || value == null) {
            callback();
            return;
        }

        // let reg = /^[1][3,4,5,7,8][0-9]{9}$/;
        let reg = /^1\d{10,10}$/;
    
        if(reg.test(value)) {
            callback();
        } else {
            callback(new Error(''));
        }
    },
    
    idCard(rule, value, callback) {
        // 1518
        if(value == '' || value == null ) {
            callback();
            return;
        }

        if(value.length != 15 && value.length != 18) {
            callback(new Error(''));
        } else {
            callback();
        }
    },
    
    credit(rule, value, callback) {
        if(value == '' || value == null ) {
            callback();
            return;
        }

        if(value.length > 4 || value > 9999) {
            callback(new Error('4!'));
        } else {
            callback();
        }
    }
};


