<template>
    <el-container>
        <el-main>    
            <bread-crumb :items="breadCrumb"></bread-crumb>
            <div class="hd"> <span class="skip"></span></div>
            <div></div>
            <div></div>
           <el-table :data="searInfoList" border style="width: 100%;margin-top:50px;">
                <el-table-column type="index" label="" align="center" width="100px" ></el-table-column>
                <el-table-column prop="name" label="" align="center" ></el-table-column>
                <el-table-column prop="address" label="" align="center" ></el-table-column>
                <el-table-column prop="address" label="" align="center" ></el-table-column>
                <el-table-column prop="address" label="" align="center"></el-table-column>
            </el-table> 
            <div class="gap-2">
                <el-button style="width: 140px" type="danger" @click="handleReject"></el-button>
                <el-button style="width: 140px" type="primary" @click="handleApprove"></el-button>
                <el-button style="width: 140px" type="" @click="handleClose"></el-button>
            </div>
            <h2 class="gap-2"></h2>
            <el-table
                :data="historyRecords"
                stripe
                style="width: 100%; margin-bottom: 40px;">
                <el-table-column
                prop="commitDate"
                label=""
                width="180">
                </el-table-column>
                <el-table-column
                prop="verifyDate"
                label=""
                width="180">
                </el-table-column>
                <el-table-column
                prop="verifyStatus"
                label="">
                </el-table-column>
                <el-table-column
                prop="rejectReason"
                label="">
                </el-table-column>
                <el-table-column
                prop="verifyPerson"
                label="">
                </el-table-column>
            </el-table> 
            <el-dialog :title="title" :visible.sync="dialogVisible" width="30%">
                <textarea name="" id="" style="width:100%;" rows="10" placeholder="" v-model="remark"></textarea>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false"> </el-button>
                    <el-button type="primary" @click="dialogVisible = false, submit()"> </el-button>
                </span>
            </el-dialog>
        </el-main>
    </el-container> 
</template>

<script>
import BreadCrumb from '@/components/common/BreadCrumb';

export default {
    name:'ProfessionAuditVerify',
    components:{BreadCrumb},
    data(){
        return {
            auditType:'1',//
            breadCrumb: [{text:''},{text: ""},{text:''}],
            historyRecords:[],// 
            searInfoList:[],
            dialogVisible:false,
            remark:'',
            title:'',
            id:this.$route.query.id//Id;
        }
    },
    created(){
        // ;
        this.$http.get(this.$apiUrl.professionAudit.detailContract+"?id="+this.id)
            .then(function(data){
                console.log('')
            })
            .catch(function(err){
                console.log(err);
            });
        // 
        this.$http.get(this.$apiUrl.professionAudit.historyAudit+"?id="+this.id)
            .then(function(data){
                console.log('');
            })
            .catch(function(err){
                console.log(err);
            });
    },
    methods:{
        handleReject(){
            this.dialogVisible=true;
            this.title='';
        },
        handleApprove(){
            this.dialogVisible=true;
            this.title='';
        },
        handleClose(){
            history.back();
        },
        submit(){
            let form={
                targetId:this.id,
                auditType:this.auditType,
                remark:this.remark
            };
            if(this.title==''){
                if(this.remark){
                    this.$http.post(this.$apiUrl.professionAudit.reject,form)
                        .then(function(data){
                            console.log('');
                            this.remark="";
                        })
                        .catch(function(err){
                            console.log(err);
                        })
                }else{
                    this.$message.error('');
                }
                
            }else{
                this.$http.post(this.$apiUrl.professionAudit.pass,form)
                .then(function(data){
                    console.log('');
                    this.remark="";
                })
                .catch(function(err){
                    console.log(err);
                })
            }  
        }
    }
}
</script>

<style scoped>
    .hd{
        margin: 20px 0;
    }
    .skip{
        color:blue;
    }
</style>





