<template>
    <div>
        <el-form ref="form" :model="form" label-width="180px" class="gap-2">
            <el-row >
                <el-col :span="12">
                    <el-form-item label="" prop="submitterId">
                        <el-select v-model="form.submitterId" placeholder="" @focus="requestSubmitList" filterable :clearable="true">
                            <el-option
                                v-for="item in submitPeopleList"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="auditTime">
                        <el-date-picker
                            v-model="form.auditTime"
                            type="daterange"
                            align="right"
                            unlink-panels
                            range-separator=""
                            start-placeholder=""
                            end-placeholder=""
                            value-format="yyyy-MM-dd"
                            format="yyyy-MM-dd">
                        </el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="auditorId">
                        <el-select v-model="form.auditorId" placeholder="" @focus="requestSubmitList" filterable :clearable="true">
                            <el-option
                                v-for="item in submitPeopleList"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="/" prop="targetName">
                        <el-input v-model="form.targetName"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <el-row :gutter="20">
            <el-col :span="2" :offset="11">
                <el-button class="reset" @click="resetForm"></el-button>
            </el-col>
            <el-col :span="2">
                <el-button type="primary" @click="search"></el-button>
            </el-col>
        </el-row>
        <div class="search-result" > {{pagination.total}}</div>
        <el-table :data="passAuditList" border style="width: 100%">
            <el-table-column prop="targetName" label="" align="center" ></el-table-column>
            <el-table-column prop="targetTypeName" label="" align="center" ></el-table-column>
            <el-table-column prop="subbmitterName" label="" align="center" ></el-table-column>
            <el-table-column prop="auditTime" label="" align="center"></el-table-column>
            <el-table-column prop="operateRemark" label="" align="center"></el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="form.currentPage"
                :page-sizes="[10, 20, 50, 100,500]"
                :page-size="form.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagination.total">
            </el-pagination>
        </div>
    </div>  
</template>

<script>
export default {
    name:'auditPass',
    data(){
        return {
            // 
            pagination:{
                total:null//
            },
            passAuditList:[],//
            submitPeopleList:[],//;
            // 
            form: {
                auditorId:'',//Id;
                auditTime:[],//
                targetState:"3",//
                targetName:'',//
                currentPage:1,//1
                pageSize:10,//10
                submitterId:'',//Id
            }
        }
    },
    created(){
        this.requestList();
    },
    methods:{
        //
        handleSizeChange(val) {
            this.form.pageSize=val;
            this.requestList();
        },
        //
        handleCurrentChange(val) {
            this.form.currentPage=val;
            this.requestList();
        },
        resetForm() {
            this.$refs.form.resetFields();
        },
        search(){
            this.form.currentPage=1;
            this.form.pageSize=10;
            this.requestList();
        },
        // 
        requestList(){
            let self=this;
            this.$http.post(this.$apiUrl.professionEnd.list,this.form,{
                loading: {
                    fullscreen: true
                }
            })
                .then(function(data){
                    self.pagination.total=data.data.data.total;
                    self.passAuditList=data.data.data.data;
                })
                .catch(function(err){
                    console.log(err,'');
                });
        },
        // ;
        requestSubmitList(){
            let self=this;
            this.$http.get(this.$apiUrl.common.employee+"?personType=9")
                .then(function(data){
                    self.submitPeopleList=data.data.data;
                })
                .catch(function(err){
                    console.log(err);
                })
        }
    }
}
</script>

<style scoped>
    .pagination{
        margin-top: 100px;
        text-align: center;
    }
    .search-result{
        margin-top: 50px;
        margin-bottom: 20px;
    }
</style>





