<template>
    <el-dialog title="" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
        <el-form :model="form" ref="form" label-width="110px" class="demo-form">
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="openBankType" class="tl">
                        <el-input v-model="form.openBankType"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="openBankBranch">
                        <el-input v-model="form.openBankBranch"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="accountName" class="tl">
                        <el-input v-model="form.accountName"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="bankAccount">
                        <el-input v-model="form.bankAccount"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
            <span slot="footer" class="dialog-footer">
            <el-button @click="submitForm" type="primary"></el-button>
            <el-button @click="resetForm"></el-button>
        </span> 
    </el-dialog>
</template>

<script>
export default {
    name:'commission',
    data(){
        return {
            companyId:'',//Id;
            dialogVisible:false,
            form:{
                bankAccount:'',//
                openBankType:'',// 
                accountName: '',//
                openBankBranch: '',// 
            },
            rules: {
                openBankType:[{ required: true, message: '', trigger: 'blur' }],
                openBankBranch: [{ required: true, message: '', trigger: 'blur' }],
                accountName: [{ required: true, message: '', trigger: 'blur' },],
                bankAccount:[{ required: true, message: '', trigger: 'blur' }]
            }
        }
    },
    methods:{
        submitForm() {
            let self=this;
            let realForm={};
            realForm.bankAccount=this.form.bankAccount;
            realForm.openBankType=this.form.openBankType;
            realForm.accountName=this.form.accountName;
            realForm.openBankBranch=this.form.openBankBranch;
            realForm.companyId=this.companyId;
            // ,Id;
            this.$http.post(this.$apiUrl.company.commission,realForm)
                .then(function(data){
                    self.$message({
                        message: '',
                        type: 'success'
                    });
                    self.$refs.form.resetFields();
                    self.dialogVisible=false;
                })
                .catch(function(err){
                    self.$message({
                        message: '',
                        type: 'error'
                    });
                });    
        },
        resetForm() {
            this.$refs.form.resetFields();
            this.dialogVisible=false;
        },
        handleClose(done) {
            // x;
            this.$confirm('')
            .then(_ => { 
                // 
                this.$refs.form.resetFields();
                done();
            })
            .catch(_ => {
                //
            });
        },
        // ;
        open(companyId){
            // ;
            let self=this;
            this.companyId=companyId;
            this.$http.get(this.$apiUrl.company.detail+"?companyId="+companyId)
                .then(function(data){
                    self.form=data.data.data;
                    self.dialogVisible=true;
                })
                .catch(function(err){
                    console.log(err);
                });
        }
    }
}
</script>

<style scoped>
    .el-dialog__footer{
        text-align: left;
    }
</style>





