export default {
    namespaced: true,
    state: {
        dividingInfo: [] // Vue
    },
    mutations: { 
        clear(state, val) {
            state.dividingInfo = [];
        },    
        updateItem(state, obj) {    
            // ,index
            let index = obj.index;
            delete obj.index;
            state.dividingInfo.splice(index, 1, obj);
        },
        updateItems(state, items) {
            state.dividingInfo = items;
        },
        removeItem(state, index) {
            state.dividingInfo.splice(index, 1);
        }
    }
};


