<template>
    <el-container>
        <el-main>
            <bread-crumb :items="breadCrumb"></bread-crumb>
            <div style="text-align:right;margin-bottom:20px;">
                <el-button type="primary" @click="goBack"></el-button>
                <el-button type="primary" @click="addFollowUp" v-show="privileges.addFollowUp">+</el-button>
            </div>
            <el-table :data="apiData" border style="width: 100%">
                <el-table-column prop="createTime" label="" align="center"></el-table-column>
                <el-table-column prop="operatorName" label="" align="center" ></el-table-column>
                <el-table-column prop="message" label="" align="center" ></el-table-column>
                <el-table-column  label="" align="center">
                    <template slot-scope="scope">
                        <el-button size="mini" type="text" @click="detail(scope.$index,scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-main>
    </el-container>
</template>

<script>
import BreadCrumb from '@/components/common/BreadCrumb';
import PrivilegeMixin from '@/utils/privilege';
export default {
    name:"FollowUp",
    components:{BreadCrumb},
    mixins: [PrivilegeMixin],
    data(){
        return {
            privilegeOption:{//
                "addFollowUp": "/followUp#addFollowUp"//
            },
            breadCrumb:[{text:''},{text:''},{text:''}],
            agencyId:this.$route.query.agencyId,
            apiData:[]
        }
    },
    created(){
        let self=this;
        this.$http.post(this.$apiUrl.agent.followUp,{agencyId:this.agencyId},{
                loading: {
                    fullscreen: true,
                }
            })
            .then(function(data){
                self.apiData=data.data.data;
            })
            .catch(function(err){
                console.log(err,'');
            })
    },
    methods:{
        addFollowUp(){
            this.$router.push({name:'AddFollowUp',query:{agencyId:this.agencyId}});
        },
        detail(index,row){
            this.$router.push({name:'FollowUpDetail',query:{id:row.id}});
        },
        goBack(){
            this.$router.push({path:"/agentManagement"});
            // history.back();
        }
    }
}
</script>

<style>

</style>





