<template>
    <div>
        <CollapsePanel class="gap-2" :expand="expand">    
            <div align-left slot="header" class="clearfix">
                <span style="margin-right: 30px"></span> <span style="margin-left: -20px;font-size: 14px; color: #4e4e4e;">()</span>    
                <el-button v-show="mode === 'edit' && status !== 'editing'" @click="handleEdit" type="primary" size="mini"></el-button>         
            <el-button v-show="mode === 'edit' && status === 'editing'" @click="handleCancel" type="danger" size="mini"></el-button>         
            <el-button v-show="mode === 'edit' && status === 'editing'" @click="handleComplete" type="success" size="mini"></el-button>                       
            </div>
            <el-form ref="form" :model="item" label-width= "180px" v-show="mode === 'create' || mode === 'edit' && status === 'editing'">
                <el-row>
                    <el-col :span="12" class="service-fee">
                        <el-form-item label="">
                            <!-- el-input -->
                            <el-input v-model="fee" type="number">
                                <template slot="append"></template>
                            </el-input><el-input v-model="month" type="number">
                                <template slot="append"></template>
                            </el-input>
                        </el-form-item>                    
                    </el-col>
                    <el-col :span="12" class="service-fee">
                        <el-form-item label="">
                            <el-input v-model="prefee" type="number">
                                <template slot="append"></template>
                            </el-input><el-input v-model="premonth" type="number">
                                <template slot="append"></template>
                            </el-input>
                        </el-form-item>                    
                    </el-col>
                </el-row>                                    
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="">
                            <el-switch
                                v-model="paymentType"
                                active-text=""
                                inactive-text=""
                                :active-value="2"
                                :inactive-value="1"
                                >
                            </el-switch>
                        </el-form-item>                    
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="" v-show="paymentType == 2">
                            <el-input v-model="count" type="number">
                                <template slot="append"></template>
                            </el-input>
                        </el-form-item>                    
                    </el-col>
                </el-row> 
            </el-form>

            <el-form :model="item" label-width= "180px" v-show="mode === 'view' || mode === 'edit' && status === ''">
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="">
                            {{fee}}{{month}}                            
                        </el-form-item>                    
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="">
                            {{prefee}}{{premonth}}                    
                        </el-form-item>                    
                    </el-col>
                </el-row>                                    
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="">
                            {{paymentType == '2' && "" || paymentType == '1' && "" || ""}}                            
                        </el-form-item>                    
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="" v-show="paymentType == 2">
                            {{count}}                            
                        </el-form-item>                    
                    </el-col>
                </el-row> 
            </el-form>
        </CollapsePanel>
    </div>
</template>
<script>
/**
 * 
 * TODO:
 */
    import CollapsePanel from '@/components/common/CollapsePanel';
    import {generateComputed} from './_Utils';
    import {mapMutations} from 'vuex';
    // 
    export default {
        name: "platformServiceFee",
        components: {CollapsePanel},
        props:  {
            item: Object,
            mode: {
                type: String,
                default: "view"
            }
        },
        data() {            
            return {
                expand: true,                
                status: "",
                innerItem: {}
            };
        },
        computed: {
            "fee": generateComputed("fee", "PlatformServiceFee", "updateFee"),
            "month": generateComputed("month", "PlatformServiceFee", "updateMonth"),
            "prefee": generateComputed("prefee", "PlatformServiceFee", "updatePrefee"),
            "premonth": generateComputed("premonth", "PlatformServiceFee", "updatePremonth"),
            "paymentType": generateComputed("paymentType", "PlatformServiceFee", "updatePaymentType"),
            "count": generateComputed("count", "PlatformServiceFee", "updateCount")            
        },
        methods: {
            handleEdit() {
                this.status = 'editing';                
                this.innerItem = Object.assign({}, this.$store.state.PlatformServiceFee);                
                
            },
            handleComplete() {
                this.status = '';     
                this.updateItem(this.innerItem);
            },
            handleCancel() {
                this.status = '';
            },    
            validate(fn) {
                this.$refs.form.validate(fn);
            },        

            ...mapMutations('PlatformServiceFee', [
                'updateItem', 
                'updateFee', 
                'updateMonth', 
                'updatePrefee', 
                'updatePremonth', 
                'updatePaymentType', 
                'updateCount', 
                'updateDividingInfo'])
        },
        watch: {
            
        }
    }
</script>
<style scoped>
.el-date-editor {
    width: 100%;
}

.service-fee .el-input-group {
    width: 50%;
}
</style>


