<template>
    <el-dialog :title="title" :visible.sync="dialogVisible" width="60%" >
        <el-form :model="form" :rules="rules" ref="form" label-width="110px">
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="name" class="tl">
                        <el-input v-model="form.name" placeholder="50" maxlength="50"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="abbreviation">
                        <el-input v-model="form.abbreviation" placeholder="50" maxlength="50"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <!--<el-form-item label="" prop="cityList" class="tl">
                        <region v-model="form.cityList" :startLevel="startLevel" :endLevel="endLevel"></region>
                    </el-form-item>-->
                    <el-form-item label="" prop="areaInfo" class="tl">
                        <territory v-model="form.areaInfo" :startLevel='startLevel' :endLevel="endLevel"></territory>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="deposit">
                        <el-input v-model.number="form.deposit" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="organizationCode" class="tl">
                        <el-input v-model="form.organizationCode" placeholder="50" maxlength="50"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item prop="businessType" label="" placeholder="">
                        <el-select v-model="form.businessType" filterable>
                                <el-option label="" :value="3"></el-option>
                                <el-option label="" :value="1"></el-option>
                                <el-option label="" :value="2"></el-option>
                            </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="bd" prop="bds"  label-width="40px" class="tl">
                <el-select v-model="form.bds" placeholder="" @focus="bdList" filterable multiple style="width:100%;" :clearable="true">
                    <el-option
                        v-for="item in bdInfoList"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="cooperationTime">
                            <el-date-picker
                                v-model="form.cooperationTime"
                                type="daterange"
                                align="right"
                                unlink-panels
                                range-separator=""
                                start-placeholder=""
                                end-placeholder=""
                                value-format="yyyy-MM-dd"
                                format="yyyy-MM-dd">
                            </el-date-picker>
                        </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="corporate" >
                        <el-input v-model="form.corporate"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" class="tl" prop="corporatePhone">
                        <el-input v-model.number="form.corporatePhone" ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="address" >
                        <el-input v-model="form.address"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="" class="tl" prop="agencyId">
                <el-select v-model="form.agencyId" placeholder="" @focus="agencyList" filterable :clearable="true">
                    <el-option label="" :value="0"></el-option>
                    <el-option
                        v-for="item in agencyInfoList"
                        :key="item.agencyId"
                        :label="item.agencyCompanyName"
                        :value="item.agencyId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item  class="tl upload">
                <upload :fileList.sync='form.files' v-if="dialogVisible" multiple></upload>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="submitForm" type="primary"></el-button>
            <el-button @click="resetForm"></el-button>
        </span>
    </el-dialog>
</template>

<script>
import Region from '@/components/common/Region';
import Territory from '@/components/common/Territory';
import Upload from '@/components/common/Upload';
import {Validator} from '@/components/agent/_Utils';
export default {
    name:'editorCompany',
    props:['companyId','title'],
    components:{Region,Upload,Territory},
    data(){
        return {
            agencyInfoList:[],
            bdInfoList:[],
            dialogVisible:false,
            startLevel:2,//
            endLevel:3,//
            form:{
                abbreviation:'',//;
                address:'',//
                agencyId:'',//Id
                bds:[],//bdIdlist
                businessType:'',//,01.2.3.
                cityId:'',//Id
                areaInfo:{},//
                corporate:'',//
                corporatePhone:'',//
                cooperationTime:[],//
                companyId:'',//Id;
                deposit:'',//
                files:[],//
                name:"",//;
                organizationCode:'',//
                provinceId:'',//Id
                resources:[]//fileName:'',// owner:'',//Id resourceKey:'',//key
            },
            // 
            rules: {
                businessType: [{ required: true, message: '', trigger: 'blur' }],
                name: [{ required: true, message: '', trigger: 'blur' }],
                abbreviation: [{ required: true, message: '', trigger: 'blur' }],
                areaInfo: [{ required: true, message: '', trigger: 'blur' }],
                deposit: [{ required: true, message: '', trigger: 'blur' },{ type: 'number', message: ''}],
                organizationCode: [{ required: true, message: '', trigger: 'blur' }],
                cooperationTime: [{ required: true, message: '', trigger: 'blur' }],
                corporatePhone:[{validator:Validator.mobile,trigger: 'blur'}]
            },
            // tipText: "(jpg/jpeg/gif/png/bmp)txt,doc,docx,xls,xlsx,ppt,pptx,pdf10M"
        }
    },
    created(){
        //;
        this.agencyList();
        this.bdList();
    },
    methods:{

        open() {
            let self=this;
            if(this.title==''){
                // ;
                this.$http.get(this.$apiUrl.company.detail+"?companyId="+this.companyId)
                    .then(function(data){
                        self.form.bds=[];//;
                        self.form.areaInfo={};
                        self.form.abbreviation=data.data.data.abbreviation;//;
                        self.form.address=data.data.data.address;//
                        self.form.agencyId=data.data.data.agencyId;//Id;
                        self.form.companyId=data.data.data.companyId;//Id;
                        self.form.name=data.data.data.name;//;
                        self.form.deposit=data.data.data.deposit;//;
                        self.form.organizationCode=data.data.data.organizationCode;//;
                        self.form.businessType=data.data.data.businessType;//;
                        self.form.cooperationTime=data.data.data.cooperationTime;//;
                        self.form.corporate=data.data.data.corporate;//;
                        self.form.corporatePhone=data.data.data.corporatePhone;//;
                        self.form.files=data.data.data.files;//;
                        for(var i in data.data.data.bdList){
                            self.form.bds.push(data.data.data.bdList[i].bd);//bd
                        };
                        // ;
                        self.form.areaInfo={
                            id:[data.data.data.provinceId,data.data.data.cityId],
                            name:data.data.data.provinceName+'-'+data.data.data.cityName
                        };
                        self.dialogVisible = true;
                })
                .catch(function(err){
                    console.log(err);
                });
            }else{
                this.form={
                    abbreviation:'',//;
                    address:'',//
                    agencyId:'',//Id;
                    bds:[],//bdIdList
                    businessType:'',//,01.2.3.
                    cityId:'',//Id
                    areaInfo:{},//;
                    corporate:'',//
                    corporatePhone:'',//
                    cooperationTime:[],//
                    companyId:'',//id;
                    deposit:'',//
                    files:[],//
                    name:"",//;
                    organizationCode:'',//
                    provinceId:'',//Id
                    resources:[],//
                };
                this.dialogVisible = true;
            }

        },
        // bdList
        bdList(){
            this.$http.get(this.$apiUrl.common.employee+"?personType=5")
                .then(data=>{
                    this.bdInfoList=data.data.data;
                }).catch(err=>{
                    console.log(err);
                })
        },
        agencyList(){
            let self=this;
            this.$http.get(this.$apiUrl.agent.list)
            .then(function(data){
                self.agencyInfoList=data.data.data;
            })
            .catch(function(err){
                console.log(err,'');
            })
        },
        handleClose() {
            this.$refs.form.resetFields();
        },
        submitForm() {
            let self=this;
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.dialogVisible=false;
                    if(this.title==''){
                        this.form.cooperationStart=this.form.cooperationTime[0];
                        this.form.cooperationEnd=this.form.cooperationTime[1];
                        let realForm=Object.assign({},this.form);
                        realForm.cityId=this.form.areaInfo.id[1];
                        realForm.resources=[];//;
                        if(realForm.files.length){
                            for(var i in realForm.files){
                                realForm.resources.push({
                                    fileName:realForm.files[i].fileName,
                                    owner:realForm.companyId,
                                    resourceKey:realForm.files[i].resourceKey
                                });
                            };
                        };
                        this.$http.post(this.$apiUrl.company.add,realForm)
                            .then(function(data){
                                self.$message({
                                    message: '',
                                    type: 'success'
                                });
                                self.$emit('editSuccess',self.form);
                            })
                            .catch(function(error){
                                self.$message({
                                    message: '',
                                    type: 'error'
                                });
                            });
                    }else{
                        if(this.form.areaInfo&&JSON.stringify(this.form.areaInfo) !== "{}"){
                            this.form.provinceId=this.form.areaInfo.id[0];
                            this.form.cityId=this.form.areaInfo.id[1];
                        };
                        this.form.cooperationStart=this.form.cooperationTime[0];
                        this.form.cooperationEnd=this.form.cooperationTime[1];
                        let realForm=Object.assign({},this.form);
                        if(realForm.files.length){
                            for(var i in realForm.files){
                                realForm.resources.push({
                                    fileName:realForm.files[i].fileName,
                                    owner:realForm.companyId,
                                    resourceKey:realForm.files[i].resourceKey
                                });
                            };
                        };
                        delete realForm.areaInfo;
                        this.$http.put(this.$apiUrl.company.add,realForm)
                            .then(function(data){
                                self.$message({
                                    message: '',
                                    type: 'success'
                                });
                                self.$emit('addSuccess',realForm);
                            })
                            .catch(function(error){
                                self.$message({
                                    message: '',
                                    type: 'err'
                                });
                            });
                    };
                        // ;
                        // this.$refs[formName].resetFields();
                }else {
                    this.$message({
                        message:"",
                        type:"error",
                        dutration:2000
                    });
                    return false;
                }
            });
        },
        resetForm() {
            this.$refs.form.resetFields();
            this.dialogVisible=false;
        },
        // ;
        fileValidator(file) {
            let isImg = file.type.indexOf('image') > -1;
            let isPdf = file.type.indexOf('pdf') > -1;
            let name = file.name.toLowerCase();
            let isTxt = name.indexOf('.txt') > -1;
            let isDoc = name.indexOf('.doc') > -1;
            let isDocx = name.indexOf('.docx') > -1;
            let isXls = name.indexOf('.xls') > -1;
            let isXlsx = name.indexOf('.xlsx') > -1;
            let isPpt = name.indexOf('.ppt') > -1;
            let isPptx = name.indexOf('.pptx') > -1;
            let isRar = name.indexOf('.rar') > -1;
            let isZip = name.indexOf('.zip') > -1;

            let isLt2M = file.size / 1024 / 1024 < 10;
            if(!isImg && !isPdf && !isTxt && !isDoc && !isDocx && !isXls && !isXlsx && !isPpt && !isPptx && !isRar && !isZip) {
                return {
                    status: 0,
                    message: "txt,doc,docx,xls,xlsx,ppt,pptx,pdf"
                };
            }
            if(!isLt2M){
                return {
                    status: 0,
                    message: "2M"
                };
            }

            return {
                status: 1
            };
        }
    }
}
</script>

<style scoped>
    .tl .el-form-item__label{
        text-align: left;
    }
    .upload .el-form-item__content{
        margin-left: 0!important;
    }
    .team-time .el-form-item__error{
        width:120px;
    }
</style>




