<template>
    <el-dialog :title="title" :visible.sync="dialogVisible" width="50%">
        <el-form :model="form" :rules="rules" ref="form" label-width="120px">
            <el-form-item label="" prop="name" >
                <el-input v-model="form.name" :disabled="disabled"></el-input>
            </el-form-item>
            <el-form-item label="" prop="storeType">
                <el-select :disabled="disabled" v-model="form.storeType" filterable placeholder="">
                        <el-option label="" :value="1"></el-option>
                        <el-option label="" :value="2"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="agencyId" v-if="form.storeType==1" >
                <el-select :disabled="disabled" v-model="form.agencyId" placeholder="" @focus="agencyList" filterable :clearable="true">
                    <el-option
                        v-for="item in agencyInfoList"
                        :key="item.agencyId"
                        :label="item.agencyCompanyName"
                        :value="item.agencyId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="companyId" v-if="form.storeType==2" >
                <el-select :disabled="disabled" v-model="form.companyId" placeholder="" @focus="companyList"  filterable :clearable="true">
                    <el-option
                        v-for="item in companyInfoList"
                        :key="item.companyId"
                        :label="item.name"
                        :value="item.companyId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="address">
                <el-input :disabled="disabled" v-model="form.address"></el-input>
            </el-form-item>
            <el-form-item class="city-dis" label="--" prop="areaInfo">
                <el-row>
                  <el-col :span="10">
                      <territory :disabled="disabled" v-model="form.areaInfo" :startLevel='startLevel' :endLevel="endLevel"></territory>
                  </el-col>
                  <el-col :span="7">
                    <el-select :disabled="disabled" v-model="form.districtId" filterable placeholder="">
                      <el-option
                        v-for="item in districtoptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>
                  </el-col>
                  <el-col :span="7">
                    <el-select :disabled="disabled" v-model="form.townId" filterable placeholder="">
                      <el-option
                        v-for="item in plateoptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>
                  </el-col>
                </el-row>
            </el-form-item>
            <el-form-item label="" prop="longlat" placeholder=",">
                <el-input :disabled="disabled" v-model="form.longlat" @blur="longlat"></el-input>
            </el-form-item>
            <el-form-item label=""  v-if="!disabled && dialogVisible">
                <upload v-if="form.resources.length>0" :fileList.sync='form.resources' multiple ref="formupload"></upload>
                <upload :fileList.sync='form.resources' v-else multiple ref="formupload"></upload>
            </el-form-item>
            <el-form-item label="" v-if="disabled && form.resources && form.resources.length!=0">
                <ul ref="imgs" class="imgsForBrower">
                  <li v-for="(file, ids) in resourcesFiles" :key="ids"><a :href="file.url">{{file.fileName}}</a></li>
                  <li v-for="(img, index) in resourcesImgs" :key="index"><img :src="img.url" alt=""></li>
              </ul>
            </el-form-item>
            <el-form-item label="">
                <el-input :disabled="disabled" type="textarea" v-model="form.remark" placeholder="(100)" rows="5" maxlength="100"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="submitForm" type="primary" v-if="!disabled"></el-button>
            <el-button @click="resetForm"></el-button>
        </span>
    </el-dialog>
</template>

<script>
import Region from '@/components/common/Region';
import Territory from '@/components/common/Territory';
import Upload from '@/components/common/Upload';
import Viewer from "viewerjs/dist/viewer";
export default {
    name:'editorStore',
    props:['storeId','title'],
    components:{Region,Territory,Upload},
    data(){
        return {
            startLevel:2,//
            endLevel:3,//
            dialogVisible:false,
            companyInfoList:[],
            longlatFormat:false,//
            form: {
                address:'',//;
                agencyId:'',//Id;
                areaLinkage:[],//
                cityId: '',//Id;
                companyId:'',//Id;
                districtId:'',//Id;
                files:[],//;
                longlat:'',//;
                name:'',//;
                provinceId:'',//Id;
                remark:'',//;
                storeType:'',//;
                townId:'',//Id;
                areaInfo:{
                  id: [],
                  name: ''
                },
                resources: [], // 
                remark: '' // 
            },
            // 
            rules: {
                companyId: [{ required: true, message: '', trigger: 'blur' }],
                agencyId:[{ required: true, message: '', trigger: 'blur' }],
                "areaInfo": [{ required: true, message: '', trigger: 'blur' }],
                address:[{ required: true, message: '', trigger: 'blur' }],
                name:[{ required: true, message: '', trigger: 'blur' }],
                storeType:[{ required: true, message: '', trigger: 'blur' }],
                longlat:[{ required: true, message: '', trigger: 'blur' }]
            },
            // 
            // startLevel:2,
            // endLevel:3,

            // 
            districtoptions: [
              // {  
              //   value: '1',
              //   label: ''
              // }
            ],
            // 
            plateoptions: []
        }
    },
    created(){
        // ;
        this.agencyList();
        // ;
        this.companyList();
    },
    methods:{
        open() {
            let self =this;
            if(this.title=='' || this.title==''){
                // ;
                this.$http.get(this.$apiUrl.store.add+"?id="+this.storeId)
                    .then(function(data){
                        try { // key
                          Object.assign(self.form, data.data.data);
                        } catch(e) {
                          self.form = data.data.data;
                          self.form.areaInfo = {
                            id: [],
                            name: ''
                          }
                          console.log(e);
                        }
                        self.form.areaLinkage=[self.form.cityId,self.form.districtId,self.form.townId];
                        self.form.areaInfo = {};
                        self.form.areaInfo.id = [];
                        self.form.areaInfo.id.push(self.form.cityId);
                        self.form.areaInfo.name = self.form.cityName;
                        self.dialogVisible = true;
                        setTimeout(() => {
                          if(self.title=='') {
                            let ele = self.$refs.imgs;
                            ele ? new Viewer(ele,{zIndex: 5000}) : '';
                          }
                        }, 600);
                    })
                    .catch(function(err){
                        console.log(err);
                    })
            }else{
                this.form= {
                    address:'',//;
                    agencyId:'',//Id;
                    // areaLinkage:[],//
                    cityId:'',//Id;
                    companyId:'',//Id;
                    districtId:'',//Id;
                    files:[],//;
                    longlat:'',//;
                    name:'',//;
                    provinceId:'',//Id;
                    remark:'',//;
                    storeType:'',//;
                    townId:'',//Id;
                    areaInfo:{
                      id: [],
                      name: ''
                    },
                    resources: [],
                    remark: ''
                }
                this.districtoptions = [];
                this.plateoptions = [];
                this.dialogVisible = true;
                this.$nextTick(() => {
                  this.$refs.form.clearValidate();  // 
                })
            }
        },
        longlat(){
            let longlatArray=this.form.longlat.split(',');
            var reg = /^[0-9]+.?[0-9]*$/;
            if(longlatArray.length==2&&reg.test(longlatArray[0])&&reg.test(longlatArray[1])){
                this.longlatFormat=true;
            }else{
                this.longlatFormat=false;
                this.$message({
                    message: '',
                    type: 'warning'
                });
            }
        },
        submitForm() {
            let self=this;
            this.longlat();
            this.$refs.form.validate((valid) => {
                if (valid) {
                    if(self.longlatFormat){
                        this.dialogVisible=false;
                        let areainfoId = self.form.areaInfo.id;
                        if(areainfoId && areainfoId.length>0){
                            self.form.cityId=areainfoId[areainfoId.length - 1];
                            // self.form.districtId=self.form.areaLinkage[1];
                            // self.form.townId=self.form.areaLinkage[2];
                        };
                        if(self.title==''){
                          self.$http.post(self.$apiUrl.store.add,self.form)
                            .then(function(data){
                                self.$message({
                                    message: '',
                                    type: 'success'
                                });
                                self.$emit('editSuccess',self.form);
                            })
                            .catch(function(err){
                                self.$message({
                                    message: '',
                                    type: 'error'
                                });
                            })
                        }else if(self.title==''){
                            self.$http.put(self.$apiUrl.store.add,self.form)
                                .then(function(data){
                                    self.$message({
                                        message: '',
                                        type: 'success'
                                    });
                                    self.$emit('addSuccess',self.form);
                                })
                                .catch(function(err){
                                    self.$message({
                                        message: '',
                                        type: 'error'
                                    });
                                })
                                .finally(() => {  //  
                                    this.reset()
                                });
                        };
                    }else{
                        self.$message({
                            message: '',
                            type: 'warning'
                        });
                    }
                    // ;
                    // this.$refs[formName].resetFields();
                } else {
                    self.$message({
                        message: '',
                        type: 'warning'
                    });
                    return false;
                }
            });
        },
        resetForm() {
            // if(this.title !== "") this.$refs.form.resetFields();
            this.reset();
            this.dialogVisible=false;
        },
        // ;
        agencyList(){
            let self=this;
            this.$http.get(this.$apiUrl.agent.list)
            .then(function(data){
                self.agencyInfoList=data.data.data;
            })
            .catch(function(err){
                console.log(err,'');
            })
        },
        // ;
        companyList(){
            let self=this;
            // ;
            this.$http.get(this.$apiUrl.company.list)
                .then(function(data){
                    self.companyInfoList=data.data.data
                })
                .catch(function(err){
                    console.log(err,'err')
                })
        },
        // 
        reset() {
          try {
            this.$refs.form.resetFields();  // 
            this.$refs.form.clearValidate();  // 
            this.$refs.formupload && this.$refs.formupload.$refs.eluploadfiles.clearFiles(); //
          } catch(e) {
            console.log(e);
          }

        },
        changeDistrict() {  // 
          let areaInfo = this.form.areaInfo;
          areaInfo.name && this.$http.get("/region/list?cityId=" + areaInfo.id[areaInfo.id.length-1]).then((data) => {
            data.data.data.forEach((item) => {
              item.label = item.regionName;
              item.value = item.regionId;
            })
            this.districtoptions = data.data.data;
          })
        },
        changePlate() {
          let areaInfo = this.form.areaInfo;
          areaInfo.name && this.$http.get("/plate/list?cityId="+ areaInfo.id[areaInfo.id.length-1] +"&regionId=" + this.form.districtId ).then((data) => {
            data.data.data.forEach((item) => {
              item.label = item.NAME;
              item.value = item.id;
            })
            this.plateoptions = data.data.data;
          })
        }
    },
    computed: {
      disabled() {
        return this.title === "";
      },
      resourcesImgs() {
        return this.form.resources && this.form.resources.filter((item) => {
          let itemurl = item.url;
          return itemurl.indexOf(".jpg")>0 || itemurl.indexOf(".jpeg")>0 || itemurl.indexOf(".bmp")>0 || itemurl.indexOf(".png")>0;
        })
      },
      resourcesFiles() {
        return this.form.resources && this.form.resources.filter((item) => {
          let itemurl = item.url;
          return itemurl.indexOf(".jpg")===-1 && itemurl.indexOf(".jpeg")===-1 && itemurl.indexOf(".bmp")===-1 && itemurl.indexOf(".png")===-1;
        })
      }
    },
    watch: {
      "form.areaInfo.name": function (newVlaue, old) {
        if(!newVlaue || old == undefined) {
          this.form.districtId = '';
          this.form.townId = '';
        }
        this.changeDistrict();
      },
      "form.districtId": function (newVlaue, old) {
        if(!newVlaue) {
          this.form.townId = '';
        }
        this.changePlate();
      },
    }
}
</script>

<style>
.el-input.is-disabled .el-input__inner {
  cursor: auto !important;
  color: #666;
  /* background: none;
  border: none; */
}
.el-textarea.is-disabled .el-textarea__inner {
  cursor: auto;
  color: #666;
  /* background: none;
  border: none; */
}
.el-input.is-disabled .el-input__icon {
  display: none
}
.imgsForBrower {
  list-style: none;
  padding: 0;
  margin:0;
}
.imgsForBrower img {
  width: 80px;
}
.el-col .el-select {
  width: 100% !important;
}
</style>




