<template>
    <el-container>
        <el-main>
            <bread-crumb :items="breadCrumb"></bread-crumb>
            <div style="text-align:right;">
                <el-button type="primary" @click="addStore" v-show="privileges.addStore">+</el-button>
            </div>
            <el-form ref="form" :model="form" label-width="180px" class="gap">
                <el-row >
                    <el-col :span="12">
                        <!--<el-form-item label="" prop="cityList">
                            <region v-model="form.cityList" :startLevel="startLevel" :endLevel="endLevel"></region>
                        </el-form-item>-->
                        <el-form-item label="" prop="areaInfo">
                            <territory v-model="form.areaInfo" :startLevel='startLevel' :endLevel="endLevel"></territory>
                        </el-form-item>
                        <el-form-item label="" prop="agencyId">
                            <el-select v-model="form.agencyId" placeholder="" @focus="agencyList" filterable>
                                <el-option label="" :value="0"></el-option>
                                <el-option
                                    v-for="item in agencyInfoList"
                                    :key="item.agencyId"
                                    :label="item.agencyCompanyName"
                                    :value="item.agencyId">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="storeType">
                            <el-select v-model="form.storeType" filterable>
                                <el-option label="" value="0"></el-option>
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="2"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="" prop="storeName">
                            <el-input v-model="form.storeName"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="cooperationTime">
                            <el-date-picker
                                v-model="form.cooperationTime"
                                type="daterange"
                                align="right"
                                unlink-panels
                                range-separator=""
                                start-placeholder=""
                                end-placeholder=""
                                value-format="yyyy-MM-dd"
                                format="yyyy-MM-dd">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item label="" prop="companyId">
                            <!--<el-input v-model="form.companyName"></el-input>-->
                            <el-select v-model="form.companyId" placeholder="" @focus="companyList"  filterable>
                                <el-option
                                    v-for="item in companyInfoList"
                                    :key="item.companyId"
                                    :label="item.name"
                                    :value="item.companyId">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <el-row :gutter="20">
                <el-col :span="2" :offset="11">
                    <el-button class="reset" @click="resetForm"></el-button>
                </el-col>
                <el-col :span="2">
                    <el-button type="primary" @click="search"></el-button>
                </el-col>
            </el-row>
            <div class="search-result"> {{summary.storeTotal}}{{summary.hASTotal}}{{summary.nASTotal}}</div>
            <el-table :data="storeInfo.data" border style="width: 100%">
                <el-table-column prop="storeId" label="id" align="center" ></el-table-column>
                <el-table-column prop="storeName" label="" align="center" ></el-table-column>
                <el-table-column prop="storeTypeName" label="" align="center" ></el-table-column>
                <el-table-column prop="agentCount" label="" align="center" ></el-table-column>
                <el-table-column prop="agencyName" label="" align="center"></el-table-column>
                <el-table-column prop="companyName" label="" align="center"></el-table-column>
                <el-table-column prop="cityName" label="" align="center"></el-table-column>
                <el-table-column prop="createTime" label="" align="center"></el-table-column>
                <el-table-column label="" width="300px" align="center">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="browserStore(scope.$index, scope.row)" type="text"> </el-button>
                        <el-button size="mini" @click="editStore(scope.$index, scope.row)" type="text" v-show="privileges.storeEdit"> </el-button>
                        <el-button size="mini" @click="qrCode(scope.$index, scope.row)" type="text"> </el-button>
                        <el-button size="mini"  type="text" @click="handleEnd(scope.$index, scope.row)" v-show="privileges.storeEndJoin"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="form.currentPage"
                    :page-sizes="[10, 20, 50, 100,500]"
                    :page-size="form.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="storeInfo.total">
                </el-pagination>
            </div>
            <!---->
                <el-dialog title="" :visible.sync="qrCodeShow" width="40%" v-if="qrCodeInfo.length">
                    <div style="text-align:center;">
                        <img :src="qrCodeInfo[2].url" alt="" v-if="!checked" style="width:60%;">
                        <img :src="qrCodeInfo[4].url" alt="" v-if="checked" style="width:60%;">
                        <div>{{qrCodeInfo[0].description}}</div>
                        <el-checkbox v-model="checked">logo</el-checkbox>
                        <div v-if="!checked">
                            <el-button type="primary" @click="noLogo1">1000*1000</el-button>
                            <el-button type="primary" @click="noLogo2">500*500</el-button>
                            <el-button type="primary" @click="noLogo3">300*300</el-button>
                        </div>
                        <div v-if="checked">
                            <el-button type="primary" @click="hasLogo1">1000*1000</el-button>
                            <el-button type="primary" @click="hasLogo2">500*500</el-button>
                            <el-button type="primary" @click="hasLogo3">300*300</el-button>
                        </div>
                    </div>
                </el-dialog>
            <!---->
            <el-dialog title="" :visible.sync="firstDialogVisible" width="30%" >
                <p></p>
                <p style="padding-left:15px;">a</p>
                <p style="padding-left:15px;">b</p>
                <span slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="prompt"></el-button>
                </span>
            </el-dialog>
            <!--<el-dialog title="" :visible.sync="secondDialogVisible" width="30%" >
                <textarea name="" id="" rows="10" placeholder="" v-model="remark" style="width:100%;"></textarea>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="continueJoin"> </el-button>
                    <el-button type="primary" @click="endJoin" > </el-button>
                </span>
            </el-dialog>-->
            <!---->
            <editor-store ref="editor"  :title="title" :storeId="storeId" @editSuccess='editSuccess' @addSuccess='addSuccess' :currentStoreInfo="currentStoreInfo"></editor-store>
        </el-main>
    </el-container>
</template>

<script>
import BreadCrumb from '@/components/common/BreadCrumb';
import EditorStore from '@/components/store/_EditorStore';
import Region from '@/components/common/Region';
import Territory from '@/components/common/Territory';
import PrivilegeMixin from '@/utils/privilege';

export default {
    name: 'StoreManagement',
    components:{BreadCrumb,EditorStore,Region,Territory},
    mixins: [PrivilegeMixin],
    data () {
        return {
            privilegeOption:{//
                "addStore": "/storeManagement#addStore",//
                "storeEdit": "/storeManagement#storeEdit",//
                "storeEndJoin": "/storeManagement#storeEndJoin"//
            },
            breadCrumb: [{text:''},{text: ""}],
            agencyInfoList:[],//
            companyInfoList:[],//
            checked:false,//logo
            startLevel:2,//
            endLevel:3,//
            qrCodeShow:false,
            qrCodeInfo:[],//;
            firstDialogVisible: false,//
            secondDialogVisible:false,//
            remark:'',//
            companyInfoIndex:'',//
            currentStoreInfo:'',//
            isTerminate:null,//;
            storeInfo:{},//
            storeId:'',//Id;
            summary:{},//summary
            title:'',//
            // 
            form: {
                agencyId:null,//Id
                cityId:null,//Id
                areaInfo:{},//
                companyId:null,//Id
                cooperationTime:null,//;
                cooperationStart:null,//
                cooperationEnd:null,//
                currentPage:1,//1
                pageSize:10,//10
                storeName: null,//
                storeType:'0',//
            }
        }
    },
    created(){
        this.requestList();
        this.agencyList();
        // ;
        this.companyList();
    },
    methods:{
        // ;
        agencyList(){
            let self=this;
            this.$http.get(this.$apiUrl.agent.list)
            .then(function(data){
                self.agencyInfoList=data.data.data;
            })
            .catch(function(err){
                console.log(err,'');
            })
        },
        // ;
        companyList(){
            let self=this;
            // ;
            this.$http.get(this.$apiUrl.company.list)
                .then(function(data){
                    self.companyInfoList=data.data.data
                })
                .catch(function(err){
                    console.log(err,'err')
                })
        },
        // ;
        addSuccess(){
            this.resetForm();
            this.requestList();
        },
        // ;
        editSuccess(editInfo){
            // ;
            this.requestList();
        },
        resetForm() {
            this.form={
                agencyId:null,//Id
                cityId:null,//Id
                areaInfo:{},//
                companyId:null,//Id
                cooperationTime:null,//;
                cooperationStart:null,//
                cooperationEnd:null,//
                currentPage:1,//1
                pageSize:10,//10
                storeName: null,//
                storeType:'0',//
            }
        },
        //
        search(){
            this.form.currentPage=1;
            this.form.pageSize=10;
            this.requestList();
        },
        //
        handleSizeChange(val) {
            this.form.pageSize=val;
            this.requestList();
        },
        //
        handleCurrentChange(val) {
            this.form.currentPage=val;
            this.requestList();
        },
        // 
        addStore(){
            // 
            this.title='';
            setTimeout(()=>{
                this.$refs.editor.open();
            },200);
        },
        //
        editStore(index, row){
            // ;
            this.storeId=row.storeId;
            this.title='';
            // ,setTimeout;
            setTimeout(()=>{
                this.$refs.editor.open();
            },200);
        },
        //
        browserStore(index, row){
            this.storeId=row.storeId;
            this.title='';
            // ,setTimeout;
            this.$nextTick(()=>{
                this.$refs.editor.open();
            });
        },
        //
        qrCode(index, row){
            let self=this;
            this.storeId=row.storeId;
            this.$http.get(this.$apiUrl.store.qrcode+"/"+this.storeId)
                .then(function(data){
                    self.qrCodeInfo=data.data.data;
                    self.qrCodeShow=true;
                })
                .catch(function(err){
                    console.log(err);
                })

        },
        //,
        handleEnd(index,row){
            this.storeId=row.storeId;
            this.isTerminate=row.isTerminate;
            if(this.isTerminate==0||this.isTerminate==2){
                this.firstDialogVisible = true;
            }else{
                if(this.isTerminate=1){
                    this.$message.warning("");
                }else{
                    this.$message.warning("");
                }
            };
        },
        // prompt;
        prompt(){
            let self=this;
            self.firstDialogVisible = false ;
            this.$prompt('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                inputType: "textarea",
                inputPlaceholder: "",
                customClass: 'store-end-join',
                inputValidator: function(val){
                    if(val&&val.length > 200) {
                        return "200";
                    }else if(!val){
                        return "";
                    }else{
                        return true;
                    }
                }
            }).then(({ value }) => {
                if(value){
                    this.$http.post(this.$apiUrl.store.terminate+"/"+this.storeId+"?remark="+value)
                    .then(function(data){
                        self.$message({
                            message: '',
                            type: 'success'
                        });
                        self.requestList();
                    })
                    .catch(function(err){
                        self.$message({
                            message: '',
                            type: 'error'
                        });
                    });
                }else{
                    self.$message({
                        message: '',
                        type: 'warning'
                    });
                }
            });
        },
        // ;
        requestList(){
            let self=this;
            // ;
            if(JSON.stringify(this.form.areaInfo&&this.form.areaInfo) !== "{}"){
                this.form.cityId=this.form.areaInfo.id[1];
            };
            let realForm=Object.assign({},this.form);
            delete realForm.areaInfo;
            // ;
            this.$http.post(this.$apiUrl.store.list,realForm,{
                loading: {
                    fullscreen: true
                }
            })
                .then(function(data){
                    self.storeInfo=data.data.data;
                })
                .catch(function(err){
                    console.log(err);
                });
            // summary;
            this.$http.post(this.$apiUrl.store.summary,realForm)
                .then(function(data){
                    self.summary=data.data.data
                })
                .catch(function(err){
                    console.log(err);
                })
        },
        // 
        noLogo1(){
            window.open(this.qrCodeInfo[0].url);
        },
        noLogo2(){
            window.open(this.qrCodeInfo[1].url);
        },
        noLogo3(){
            window.open(this.qrCodeInfo[2].url);
        },
        hasLogo1(){
            window.open(this.qrCodeInfo[3].url);
        },
        hasLogo2(){
            window.open(this.qrCodeInfo[4].url);
        },
        hasLogo3(){
            window.open(this.qrCodeInfo[5].url);
        }
    }
}
</script>

<style scoped>
    .el-select{
        width:100%;
    }
    .el-date-editor.el-range-editor.el-input__inner.el-date-editor--daterange{
        width:100%;
    }
    .search-result{
        color:gray;
        margin-bottom: 20px;
        margin-top: 50px;
    }
    .pagination{
        margin-top: 100px;
        text-align: center;
    }
    .el-button{
        margin-left: 0 !important;
    }
</style>

<style>
    /*scopedprompt*/
    .store-end-join textarea{
        height:100px;
    }
</style>





