<template>
    <div>
        <div class="search-result" >{{pagination.total}}</div>
        <el-table :data="waitAuditList" border style="width: 100%">
            <el-table-column prop="name" label="" align="center" ></el-table-column>
            <el-table-column prop="cityName" label="" align="center" ></el-table-column>
            <el-table-column prop="submitterName" label="" align="center" ></el-table-column>
            <el-table-column  label="" align="center" >
                <template slot-scope="scope">
                    <div><span class="circle"></span><span>{{scope.row.auditTypeName}}</span></div>
                </template>
            </el-table-column>
            <el-table-column prop="submitTime" label="" align="center"></el-table-column>
            <el-table-column prop="endReason" label="" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" @click="audit(scope.$index, scope.row)" type="text" v-show="privileges.materialAudit"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pagination.currentPage"
                :page-sizes="[10, 20, 50, 100,500]"
                :page-size="pagination.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagination.total">
            </el-pagination>
        </div>
    </div>  
</template>

<script>
import PrivilegeMixin from '@/utils/privilege';

export default {
    name:'waitAudit',
    mixins: [PrivilegeMixin],
    data(){
        return {
            privilegeOption:{//
                "materialAudit":"/materialAuditResult#materialAudit"
            },
            auditType:"1",//12
            targetState:"1",//1.23
            // 
            pagination:{
                currentPage:1,//1;
                pageSize:10,//10
                total:10//
            },
            waitAuditList:[]//;
        }
    },
    created(){
        this.requestList();
    },
    methods:{
        //
        handleSizeChange(val) {
            this.pagination.pageSize=val;
            this.requestList();
        },
        //
        handleCurrentChange(val) {
            this.pagination.currentPage=val;
            this.requestList();
        },
        // ;
        audit(index,row){
            this.$router.push({name:"MaterialVerify",params:{id:row.id}});
        },
        // ;
        requestList(){
            let self=this;
            let requestInfo={
                auditType:this.auditType,
                currentPage:this.pagination.currentPage,
                pageSize:this.pagination.pageSize,
                targetState:this.targetState
            }
            this.$http.post(this.$apiUrl.agent.firstWaitAuditList,requestInfo,{
                loading: {
                    fullscreen: true,
                }
            })
                .then(function(data){
                    self.pagination.total=data.data.data.total;
                    self.waitAuditList=data.data.data.data;
                })
                .catch(function(err){
                    console.log(err);
                });
        }
    }
}
</script>

<style scoped>
    .pagination{
        margin-top: 100px;
        text-align: center;
    }
    .search-result{
        margin:50px 0 20px 0;
    }
</style>





