<template>
    <el-container>
        <el-main>    
            <bread-crumb :items="breadCrumb"></bread-crumb>
            <div class="tabs">
                <div class="tab" :class="{agent:agent}" @click="agentTab">
                    <p class="account">{{endTotal.terminatedAgency}}</p>
                    <p></p>
                </div>
                <div class="tab" :class="{company:company}" @click="companyTab">
                    <p class="account">{{endTotal.terminatedCompany}}</p>
                    <p></p>
                </div>
                <div class="tab" :class="{store:store}" @click="storeTab">
                    <p class="account">{{endTotal.terminatedStore}}</p>
                    <p></p>
                </div>
                <p style="clear:both;"></p>
            </div>
            <!---->
            <agent-end-list v-if="agent"></agent-end-list>
            <!---->
            <company-end-list v-if="company"></company-end-list>
            <!---->
            <store-end-list v-if="store"></store-end-list>
        </el-main>
    </el-container>
</template>

<script>
import BreadCrumb from '@/components/common/BreadCrumb';
import AgentEndList from '@/components/teamEnd/_AgentEndList';
import CompanyEndList from '@/components/teamEnd/_CompanyEndList';
import StoreEndList from '@/components/teamEnd/_StoreEndList';

export default {
    name:'TeamEnd',
    components:{BreadCrumb,AgentEndList,CompanyEndList,StoreEndList},
    data(){
        return {
            breadCrumb: [{text:''},{text: ""}],
            agent:true,
            company:false,
            store:false,
            endTotal:{},//;
            label:'',
            // 
            pagination:{
                currentPage:1,//1;
                pageSize:10,//10
                total:null//
            }
        }
    },
    created(){
        let self=this;
        this.$http.get(this.$apiUrl.teamEnd.summary)
            .then(function(data){
                self.endTotal=data.data.data;
            })
            .catch(function(err){
                console.log(err);
            });
    },
    methods:{
        agentTab(){
            this.agent=true;
            this.company=false;
            this.store=false;
            this.label="";
            this.pagination.currentPage=1;
            this.pagination.pageSize=10;
        },
        companyTab(){
            this.agent=false;
            this.company=true;
            this.store=false;
            this.label="";
            this.pagination.currentPage=1;
            this.pagination.pageSize=10;
        },
        storeTab(){
            this.agent=false;
            this.company=false;
            this.store=true;
            this.label="";
            this.pagination.currentPage=1;
            this.pagination.pageSize=10;
        },
        // ;
        detail(){
            // ;
            if(this.agent){};
            // ;
            if(this.company){};
            // ;
            if(this.store){};
        },
        //
        handleSizeChange(val) {
            this.pagination.pageSize=val;
        },
        //
        handleCurrentChange(val) {
            this.pagination.currentPage=val;
        },
    }
}
</script>

<style scoped>
    p{
        line-height: 40px;
        margin:0;
    }
    .tabs{
        margin-top: 20px;
        margin-left: 50px;
    }
    .tabs div{
        border: 1px solid black;
        float: left;
        height:80px;
        text-align: center;
        width:120px;
    }
    .tab +.tab{
        margin-left: 50px;
    }
    .account{
        color:green;
    }
    .agent{
        border: 1px solid red!important;
    }
    .agent .account{
        color:red;
    }
    .company{
        border: 1px solid red!important;
    }
    .company .account{
        color:red;
    }
    .store{
        border: 1px solid red!important;
    }
    .store .account{
        color:red;
    }
    .search-result{
        margin:50px 0 20px 0;
    }
    .block{
        margin-top: 100px;
        text-align: center;
    }
</style>





