<template>
    <div>
        <el-form ref="form" :model="form" label-width="180px" class="gap-2">
            <el-row >
                <el-col :span="12">
                    <el-form-item label="" prop="submitterId">
                        <el-select v-model="form.submitterId" placeholder="" @focus="requestSubmitList" filterable :clearable="true">
                            <el-option
                                v-for="item in submitPeopleList"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="auditTime">
                            <el-date-picker
                                v-model="form.auditTime"
                                type="daterange"
                                align="right"
                                unlink-panels
                                range-separator=""
                                start-placeholder=""
                                end-placeholder=""
                                value-format="yyyy-MM-dd"
                                format="yyyy-MM-dd">
                            </el-date-picker>
                        </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="auditorId">
                        <el-select v-model="form.auditorId" placeholder="" @focus="requestSubmitList" filterable :clearable="true">
                            <el-option
                                v-for="item in submitPeopleList"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="targetId">
                        <!--<el-input v-model="form.targetName"></el-input>-->
                        <el-select v-model="form.targetId" placeholder="" @focus="agencyList" filterable :clearable="true">
                            <el-option
                                v-for="item in agencyInfoList"
                                :key="item.agencyId"
                                :label="item.agencyCompanyName"
                                :value="item.agencyId">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <el-row :gutter="20">
            <el-col :span="2" :offset="11">
                <el-button class="reset" @click="resetForm"></el-button>
            </el-col>
            <el-col :span="2">
                <el-button type="primary" @click="search"></el-button>
            </el-col>
        </el-row>
        <div class="search-result" >{{rejectList.total}}</div>
        <el-table :data="rejectList.data" border style="width: 100%">
            <el-table-column prop="name" label="" align="center" ></el-table-column>
            <el-table-column prop="typeName" label="" align="center" ></el-table-column>
            <el-table-column prop="submitterName" label="" align="center" ></el-table-column>
            <el-table-column prop="auditorName" label="" align="center"></el-table-column>
            <el-table-column prop="auditTime" label="" align="center"></el-table-column>
            <el-table-column prop="auditRemark" label="" align="center"></el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="form.currentPage"
                :page-sizes="[10, 20, 50, 100,500]"
                :page-size="form.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="rejectList.total">
            </el-pagination>
        </div>
    </div>  
</template>

<script>
export default {
    name:'auditNoPass',
    data(){
        return {
            agencyInfoList:[],//;
            // 
            form: {
                auditorId:'',//Id;
                auditName:'',//
                auditType:'1',//
                auditTime:[],//
                targetId:'',//Id
                currentPage:1,//1
                pageSize:10,//10
                submitterId:'',//Id
                submitName:'',//
                targetState:'2',//
            },
            rejectList:{},//
            submitPeopleList:[]//;
        }
    },
    created(){
        this.requestList();
        this.requestSubmitList();
        this.agencyList();
    },
    methods:{
        //
        handleSizeChange(val) {
            this.form.pageSize=val;
            this.requestList();
        },
        //
        handleCurrentChange(val) {
            this.form.currentPage=val;
            this.requestList();
        },
        resetForm() {
            this.$refs.form.resetFields();
        },
        search(){
            this.form.currentPage=1;
            this.form.pageSize=10;
            this.requestList();
        },
        // 
        requestList(){
            let self=this;
            this.$http.post(this.$apiUrl.agent.firstWaitAuditList,this.form,{
                loading: {
                    fullscreen: true,
                }
            })
                .then(function(data){
                    self.rejectList=data.data.data;
                })
                .catch(function(err){
                    console.log(err,'');
                });
        },
        // ;
        agencyList(){
            let self=this;
            this.$http.get(this.$apiUrl.agent.list)
            .then(function(data){
                self.agencyInfoList=data.data.data;
            })
            .catch(function(err){
                console.log(err,'');
            })
        },
        // ;
        requestSubmitList(){
            let self=this;
            this.$http.get(this.$apiUrl.common.employee+"?personType=9")
                .then(function(data){
                    self.submitPeopleList=data.data.data;
                })
                .catch(function(err){
                    console.log(err);
                })
        }
    }
}
</script>

<style scoped>
    .pagination{
        margin-top: 100px;
        text-align: center;
    }
    .search-result{
        margin:50px 0 20px 0;
    }
</style>





