<template>
    <div>
        <div class="search-result"> {{agencyInfo.total}}</div>
        <el-table :data="agencyInfo.data" border style="width: 100%">
            <el-table-column prop="name" label="" align="center" >
                <template slot-scope="scope">
                    <el-button size="mini" @click="detail(scope.$index, scope.row)" type="text">{{scope.row.name}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="submitterName" label="" align="center" ></el-table-column>
            <el-table-column prop="auditorName" label="" align="center" ></el-table-column>
            <el-table-column prop="auditTime" label="" align="center" ></el-table-column>
            <el-table-column prop="remark" label="" align="center"></el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pagination.currentPage"
                :page-sizes="[10, 20, 50, 100,500]"
                :page-size="pagination.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="agencyInfo.total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    name:'AgentEndList',
    data(){
        return{
            agencyInfo:{},
            // 
            pagination:{
                currentPage:1,//1;
                pageSize:10,//10
            }
        }
    },
    created(){
        this.requestList();
    },
    methods:{
        // ;
        detail(index,row){
            this.$router.push({name:'AgentDetail',params:{id:row.id}});
        },
        //
        handleSizeChange(val) {
            this.pagination.pageSize=val;
            this.requestList();
        },
        //
        handleCurrentChange(val) {
            this.pagination.currentPage=val;
            this.requestList();
        },
        // ;
        requestList(){
            let self=this;
            let form={
                currentPage:this.pagination.currentPage,
                pageSize:this.pagination.pageSize,
                type:"1"
            }
            this.$http.post(this.$apiUrl.teamEnd.tabList,form,{
                loading: {
                    fullscreen: true
                }
            })
            .then(function(data){
                self.agencyInfo=data.data.data;
            })
            .catch(function(err){
                console.log(err);
            });
        }
    }
}
</script>

<style scoped>
    .pagination{
        margin-top: 100px;
        text-align: center;
    }
    .search-result{
        margin:50px 0 20px 0;
    }
</style>





