<template>
    <el-container>
        <el-main>
            <bread-crumb :items="breadCrumb"></bread-crumb>
            <div class="gap">
                <div></div>
                <el-input type="textarea" v-model="message" rows="10" placeholder="(1000)" maxlength="1000"></el-input>
                <div class="gap-2">
                    <upload :fileList.sync='file' :limit="1"></upload>
                </div>
            </div>
            <div style="text-align:center;margin-top:40px;">
                <el-button type="primary" @click="cancel" style="margin-right:20px;"></el-button>
                <el-button type="primary" @click="submit"></el-button>
            </div>
        </el-main>
    </el-container>
</template>

<script>
import BreadCrumb from '@/components/common/BreadCrumb';
import Upload from '@/components/common/Upload';
export default {
    name:'AddFollowUp',
    components:{BreadCrumb,Upload},
    data(){
        return {
            agencyId:this.$route.query.agencyId,
            breadCrumb: [{text:''},{text: ""},{text:''},{text:''}],
            message:'',
            file:[],
            form:{},//
            resource:{
                fileName:'',//
                owner:'',//Id
                resourceKey:'',//key
            },//
            resourceKey:'',//key;
        }
    },
    methods:{
        cancel(){
            history.back();
        },
        submit(){
            let self=this;
            if(this.file.length){
                this.form={
                    agencyId:this.agencyId,
                    message:this.message,
                    resource:{
                        fileName:this.file[0].fileName,//
                        resourceKey:this.file[0].resourceKey,//key
                    },
                    resourceKey:this.file[0].resourceKey
                };
            }else{
                this.form={
                    agencyId:this.agencyId,
                    message:this.message
                }
            };
            if(this.message){
                this.$http.put(this.$apiUrl.agent.followUp,this.form)
                .then(function(data){
                    self.message="";
                    self.$message({
                        message: '',
                        type: 'success',
                        duration:1000
                    });
                    setTimeout(function(){
                        self.$router.push({name:"FollowUp",query:{agencyId:self.agencyId}});
                    },1000);
                })
                .catch(function(err){
                    console.log(err)
                })
            }else{
                alert('');
            }      
        }
    }
}
</script>

<style>

</style>





