import axios from 'axios';
import apiUrl from '@/configs/api.js';
import {getEnv} from '@/utils/env';
import {Message, Loading} from 'element-ui';

export default {    
    install(Vue){
        let UNLOGIN = 1100;
        let SUCCESS = 1;        

        // let host = location.host;
        // let env = host.indexOf('localhost') > -1 ? "dev" : "prod";
        // for(let key in apiUrl.baseUrl){
        //     if(host.indexOf(key) > -1){
        //         env = key;
        //         break;
        //     }
        // }
        let env = getEnv();
        let baseUrl = apiUrl.baseUrl[env];
        axios.defaults.baseURL = baseUrl;
        let loading = null;

        Vue.http = Vue.prototype.$http = axios;
        axios.interceptors.response.use(function(res){
            // res.data.status,
            // if(res.data && res.data.status == UNLOGIN){
            //      window.parent && window.parent !== window && window.parent.location.reload();                
            // }
            // loading
            if(res.config.loading && loading){
                //res.config.loading.instance.close();\
                loading.total--;
                if(loading.total <= 0){
                    loading.close();
                    loading = null;
                }
            }

            if(res.headers['content-type'].indexOf('text/html')>-1) {
                // ajaxjsonhtml
                window.parent && window.parent !== window && window.parent.location.reload();                
                return;
            }

            // res.data.status
            if(res.data && res.data.status != SUCCESS){// 
                // Vue.message.error(res.data.message || "");
                Message.error(res.data.message || "");
                return Promise.reject(res.data);
            }
            return res;
        },function(error,arg){
            if(error.config.loading && loading){
                // error.config.loading.instance.close();
                loading.total--;
                if(loading.total <= 0){
                    loading.close();
                    loading = null;
                }
            }
            Message.error(res.data.message || "");
            return Promise.reject(error);
        });
        axios.interceptors.request.use(function(config){
            // JSON
            // console.log("config:", config);
            // loading
            if(config.loading) {
                if(!loading) {
                    loading = Loading.service(config.loading);
                    loading.total = 0;
                }
                loading.total++;// totalloading
            }            
            return Object.assign(config, {headers: { /*'X-Requested-With': 'XMLHttpRequest'*/}});
        });

        Vue.apiUrl = Vue.prototype.$apiUrl = apiUrl;
        /**
         * axiosaxios
         * getFullUrl
         */         
        Vue.apiUrl.getFullUrl = function(url){
            return baseUrl + url;
        };
    }
};


