        2.        
-->
<template>
    <el-container>
        <el-main>    
            <bread-crumb :items="breadCrumb"></bread-crumb>
            <div style="text-align:right;">
                <el-button type="primary" @click="addCompany" v-show="privileges.addCompany">+</el-button>
            </div>     
            <el-form ref="form" :model="form" label-width="180px" class="gap" label-position="right">
                <el-row>
                    <el-col :span="12">
                        <!--<el-form-item label="" prop="cityList">
                            <region v-model="form.cityList" :startLevel="startLevel" :endLevel="endLevel"></region>
                        </el-form-item>-->
                        <el-form-item label="" prop="areaInfo">
                            <territory v-model="form.areaInfo" :startLevel='startLevel' :endLevel="endLevel"></territory>
                        </el-form-item>
                        <el-form-item label="" prop="cooperationTime">
                            <el-date-picker
                                v-model="form.cooperationTime"
                                type="daterange"
                                align="right"
                                unlink-panels
                                range-separator=""
                                start-placeholder=""
                                end-placeholder=""
                                value-format="yyyy-MM-dd"
                                format="yyyy-MM-dd">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item label="" prop="businessType">
                            <el-select v-model="form.businessType" filterable>
                                <el-option label="" value="0"></el-option>
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="2"></el-option>
                                <el-option label="" value="3"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="storeAddress">
                            <el-input v-model="form.storeAddress"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="" prop="name">
                            <el-input v-model="form.name"></el-input>
                        </el-form-item>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="" prop="searchType">
                                    <el-select v-model="form.searchType" filterable @change="searchType">
                                        <el-option label="" value="0"></el-option>
                                        <el-option label="" value="1"></el-option>
                                        <el-option label="" value="2"></el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12" v-if="form.searchType==1">
                                <el-form-item prop="searchDay" label-width="0">
                                    <el-input v-model="form.searchDay" placeholder=""></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12" v-if="form.searchType==2">
                                <el-form-item prop="searchDate" label-width="0">
                                    <el-date-picker
                                        format="yyyy-MM-dd"
                                        v-model="form.searchDate"
                                        style="width:100%;"
                                        type="date"
                                        placeholder=""
                                        value-format="yyyy-MM-dd">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-form-item label="" prop="storeName" >
                            <el-input v-model="form.storeName"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="agencyId">
                            <!--<el-input v-model="form.agency"></el-input>-->
                            <el-select v-model="form.agencyId" placeholder="" @focus="agencyList" filterable :clearable="true">
                                <el-option label="" :value="0"></el-option>
                                <el-option
                                    v-for="item in agencyInfoList"
                                    :key="item.agencyId"
                                    :label="item.agencyCompanyName"
                                    :value="item.agencyId">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <el-row :gutter="20">
                <el-col :span="2" :offset="11">
                    <el-button class="reset" @click="resetForm"></el-button>
                </el-col>
                <el-col :span="2">
                    <el-button type="primary" @click="search"></el-button>
                </el-col>
            </el-row>
            <div class="search-result"> {{summary.companyTotal}}{{summary.hATotal}}{{summary.nATotal}}</div>
            <el-table :data="companyInfoList" border style="width: 100%">
                <el-table-column type="index" label="" align="center" width="100" ></el-table-column>
                <el-table-column prop="city" label="" align="center" ></el-table-column>
                <el-table-column prop="companyName" label="" align="center" ></el-table-column>
                <el-table-column prop="companyAbbreviation" label="" align="center" ></el-table-column>
                <el-table-column prop="agencyName" label="" align="center"></el-table-column>
                <el-table-column prop="cooperationStart" label="" align="center"></el-table-column>
                <el-table-column prop="cooperationEnd" label="" align="center"></el-table-column>
                <el-table-column prop="bdName" label="bd" align="center"></el-table-column>
                <el-table-column prop="createTime" label="" align="center"></el-table-column>
                <el-table-column prop="createrName" label="" align="center"></el-table-column>
                <el-table-column prop="name" label="" width="300px" align="center">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="editorCompany(scope.$index, scope.row)" type="text" v-show="privileges.companyEdit"> </el-button>
                        <el-button size="mini" @click="bankAccount(scope.$index, scope.row)" type="text" v-show="privileges.comnAccSet"> </el-button>
                        <el-button size="mini"  type="text" @click="handleEnd(scope.$index, scope.row)" v-show="privileges.companyEndJoin"></el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="storeNum" label="" align="center"></el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="form.currentPage"
                    :page-sizes="[10, 20, 50, 100,500]"
                    :page-size="form.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="summary.companyTotal">
                </el-pagination>
            </div>
            <el-dialog title="" :visible.sync="firstDialogVisible" width="30%" >
                <p>1</p>
                <p>2</p>
                <p>3</p>
                <span slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="prompt"></el-button>
                </span>
            </el-dialog>
            <!---->
            <editor-company ref="editor" :companyId="companyId" :title="title" @editSuccess='editSuccess' @addSuccess='addSuccess'></editor-company>
            <!---->
            <commission ref="commission"></commission>
        </el-main>
    </el-container> 
</template>

<script>
import EditorCompany from '@/components/company/_EditorCompany';
import Commission from '@/components/company/_Commission';
import BreadCrumb from '@/components/common/BreadCrumb';
import Region from '@/components/common/Region';
import Territory from '@/components/common/Territory';
import PrivilegeMixin from '@/utils/privilege';

export default {
    name: 'CompanyManagement',
    components:{EditorCompany,Commission,BreadCrumb,Region,Territory},
    mixins: [PrivilegeMixin],
    data () {
        return {
            privilegeOption:{//
                "addCompany": "/companyManagement#addCompany",//
                "companyEdit": "/companyManagement#companyEdit",//
                "comnAccSet": "/companyManagement#comnAccSet",//
                "companyEndJoin": "/companyManagement#companyEndJoin"//
            },
            breadCrumb: [{text:''},{text: ""}],//
            agencyInfoList:[],//
            companyId:'',//Id;
            companyInfoList:[],//
            companyInfoIndex:'',//
            currentCompanyInfo:'',//
            endLevel:3,//
            firstDialogVisible: false,//
            isTerminate:null,//;
            startLevel:2,//
            summary:{},//summary
            title:'',//
            // 
            form:{
                agencyId:null,//id
                businessType:'0',//,01.2.3.
                cityId:null,//Id
                areaInfo:{},//
                cooperationTime:null,//
                currentPage:1,//1
                pageSize:10,//10
                name:null,//
                searchDate:null,//
                searchDay:null,// 
                searchType:null,//
                storeAddress:null,//
                storeName:null//
            }
        }
    },
    created(){
        this.requestList();
        this.agencyList();
    },
    methods:{
        // ;
        agencyList(){
            let self=this;
            this.$http.get(this.$apiUrl.agent.list)
            .then(function(data){
                self.agencyInfoList=data.data.data;
            })
            .catch(function(err){
                console.log(err,'');
            })
        },
        // prompt;
        prompt(){
            let self=this;
            self.firstDialogVisible = false ;
            this.$prompt('', '', {
                confirmButtonText: '',
                cancelButtonText: '', 
                inputType: "textarea",  
                inputPlaceholder: "", 
                customClass: 'end-join', 
                inputValidator: function(val){
                    if(val&&val.length > 200) {
                        return "200";
                    }else if(!val){
                        return "";
                    }else{
                        return true;
                    }  
                }   
            }).then(({ value }) => {                                
                if(value){
                    this.$http.post(this.$apiUrl.company.terminate+"/"+this.companyId+"?remark="+value)
                    .then(function(data){
                        self.$message({
                            message: '',
                            type: 'success'
                        });
                        self.requestList();
                    })
                    .catch(function(err){
                        self.$message({
                            message: '',
                            type: 'error'
                        });
                    });
                }else{
                    self.$message({
                        message: '',
                        type: 'warning'
                    });
                }
            });
        },
        // searchType
        searchType(){
            if(this.form.searchType==1){
                this.form.searchDay='15';
                this.form.searchDate=null;
            }else if(this.form.searchType==2){
                let date = new Date();
                let seperator1 = "-";
                let year = date.getFullYear();
                let month = date.getMonth() + 1;
                let strDate = date.getDate();
                if (month >= 1 && month <= 9) {
                    month = "0" + month;
                };
                if (strDate >= 0 && strDate <= 9) {
                    strDate = "0" + strDate;
                };
                this.form.searchDate = year + seperator1 + month + seperator1 + strDate;
                this.form.searchDay=null;
            }else{
                this.form.searchDay=null;
                this.form.searchDate=null;
            }
        },
        // ;
        addSuccess(addInfo){
            // ;
            // this.$refs.form.resetFields();
            this.resetForm();
            this.form.pageSize=10;
            this.form.currentPage=1;
            this.requestList();
        },
        resetForm() {
            // this.$refs.form.resetFields();//
            this.form={
                agencyId:null,//Id
                businessType:'0',//,01.2.3.
                cityId:null,//Id
                areaInfo:{},//
                cooperationTime:null,//
                currentPage:this.form.currentPage,//1
                pageSize:this.form.pageSize,//10
                name:null,//
                searchDate:null,//
                searchDay:null,// 
                searchType:null,//
                storeAddress:null,//
                storeName:null//
            }
        },
        //
        search(){
            this.form.currentPage=1;
            this.form.pageSize=10;
            this.requestList();
        },
        //
        handleSizeChange(val) {
            this.form.pageSize=val;
            // 
            this.requestList();
        },
        //
        handleCurrentChange(val) {
            this.form.currentPage=val;
            //
            this.requestList();
        },
        // 
        addCompany(){
            //  
            this.title='';            
            setTimeout(()=>{
                this.$refs.editor.open();
            },200); 
        },
        //
        editorCompany(index, row){
            //;
            this.companyInfoIndex=(this.form.currentPage-1)*this.form.pageSize+index;
            // ;
            this.companyId=row.companyId;
            this.title='';
            // ,setTimeout;
            setTimeout(()=>{
                this.$refs.editor.open();
            },200);  
        },
        // ;
        editSuccess(editInfo){
            this.requestList();
        },
        // 
        bankAccount(index, row){
            this.companyId=row.companyId;
            this.$refs.commission.open(this.companyId);
        },
        //,
        handleEnd(index,row){
            
            this.companyId=row.companyId;
            this.isTerminate=row.isTerminate;
            if(this.isTerminate==0||this.isTerminate==2){
                this.firstDialogVisible = true;
            }else{
                if(this.isTerminate=1){
                    this.$message.warning("");
                }else{
                    this.$message.warning("");
                }
            };
        },
        // 
        requestList(){
            let self=this;
            // ;
            if(JSON.stringify(this.form.areaInfo&&this.form.areaInfo) !== "{}"){
                this.form.cityId=this.form.areaInfo.id[1];
            };
            let realForm=Object.assign({},this.form);
            delete realForm.areaInfo;//areaInfo
            delete realForm.searchType;//
            // ;
            this.$http.post(this.$apiUrl.company.list,realForm,{
                loading: {
                    fullscreen: true
                }
            })
                .then(function(data){
                    self.companyInfoList=data.data.data.data;
                })
                .catch(function(err){
                    console.log(err,'');
                });
            // summary;
            this.$http.post(this.$apiUrl.company.summary,realForm)
                .then(function(data){
                    self.summary=data.data.data;
                })
                .catch(function(err){
                    console.log(err);
                });
        }
    }
}
</script>

<style scoped>
    .el-select{
        width:100%;
    }
    .el-form-item .el-form-item__label {
        text-align: right;
    }
    .el-date-editor.el-range-editor.el-input__inner.el-date-editor--daterange{
        width:100%;
    }
    .search-result{
        color:gray;
        margin-bottom: 20px;
        margin-top: 50px;
    }
    .pagination{
        margin-top: 100px;
        text-align: center;
    }
    .el-button{
        margin-left: 0 !important;
    }
    .el-dialog__footer{
        text-align: right;
    }
</style>

<style>
    /*scopedprompt*/
    .end-join textarea{
        height:100px;
    }
</style>






